<template>
    <div>
        
        
        <div id="login_wrapper" style="margin:0;overflow: scroll;">
            <div class="login_box topspace">
                <div class="login_box_inner">
                    <h2>회원가입</h2>
                    <div><input type="text" class="login_input" v-model="member.id" placeholder="아이디입력"></div>
                    <div><input type="password" class="login_input" v-model="member.memberPw" placeholder="비밀번호입력"></div>
                    <div><input type="password" class="login_input" v-model="memberPwRe" placeholder="비밀번호 확인"></div>
                    
                    <div>
                        <select class="login_select" v-model="member.memberCorpCode.codeId">
                            <option  value="">상조를 선택해 주세요.</option>
                            <option  v-for="(type, index) in getCodeList('CORP')" :key="index" :value="type.value">{{ type.text }}</option>
                        </select>
                    </div>
                    <div class="login_info_input">
                        <input type="password" class="login_input" style="width:100%;" v-model="corpPassword" placeholder="상조회사 비밀번호 입력">
                        초기비밀번호 0000
                    </div>
                    <div><input type="text" class="login_input" v-model="member.memberName" placeholder="실명을 입력해주세요"></div>
                    <div class="address"><input type="text" class="login_input" v-model="member.memberPhone" placeholder="전화번호(숫자만 입력해주세요)"  @keypress="onlyNumber"  @input="changePhone" maxlength="11"><span><a  @click="certifyTrans">인증번호전송</a></span></div>
                    <div class="address"><input type="text" class="login_input" v-model="certifyNumber" placeholder="인증번호입력" @keypress="onlyNumber" maxlength="6" ><span><a  @click="certifyCertify">인증하기</a></span></div>
                    

                    <div v-if="false" class="agree">
                    <br/><br/>
                        <input type="checkbox" name="obituaryleader" v-model="masterFlag"> 상조 대표 아이디
                        <br/>
                        ※상조 대표 아이디 체크시, 상조내 모든 부고를 확인하실 수 있습니다.
                    </div>
                    
                    <button class="btn_login" @click="submitTerm">가입완료</button>
                </div>
                    <TermPopup v-if="agreeFlag" :method="submit"/>
            </div>
        </div>
    </div>
</template>

<script>

import '@/assets/js/jquery-1.9.1.min.js';
import '@/assets/js/ux_common.js';
import myMixin from '@/mixin/mixin'
import  TermPopup from '@/components/common/TermPopup'
export default {
  name: 'JoinSolo',
  mixins: [myMixin],
  components: { 
      TermPopup
	},
    data(){
        return{
            masterFlag:false,
            corpPassword:'',
            member: {
                id: "",
                memberAddr: "",
                memberBirth:null,
                memberClass:"MEMBER",
                memberName:"",
                memberPhone:"",
                memberPush:null,
                memberPushYn:'Y',
                memberPw: "",
                memberType:"NORMAL",
                memberCorpMaster:'N',
                memberAttr:{
                    codeId:"TYPE-1" 
                },
                memberUseYN:{
                    codeId:"SUCCESS-0" 
                },//솔로는 무조건 승인
                memberCorpCode:{
                    codeId:''
                },
                memberMarketingAgree:false
            },
            memberPwRe:null,
            files:[],
            certifyBeforeNumber:'',
            certifyTransFlag:false,
            certifyCertifyFlag:false,
            certifyNumber:'',
            agreeFlag:false
        }
    },
  	props: {
  	},
   	mounted() {
      //let recaptchaScript = document.createElement('script')
      //recaptchaScript.setAttribute('src', 'https://www.google.com/recaptcha/api.js')
      //document.head.appendChild(recaptchaScript)
    },
    methods:{
        changePhone(){
            
            this.certifyTransFlag = false
            this.certifyBeforeNumber = ''
            this.certifyCertifyFlag = false
        },
        certifyCertify(){
            if(this.certifyBeforeNumber+"" !== this.certifyNumber+""){
                alert("인증번호가 일치하지 않습니다.");
            }else{
                this.certifyCertifyFlag = true
                alert('인증되었습니다.')
            }

        },
        certifyTrans(){
            let result = Math.floor(Math.random() * 1000000)+100000;
            if(result>1000000){
                result = result - 100000;
            }
            if(!this.phoneCheck(this.member.memberPhone)){

                return;
            }
    
            this.certifyBeforeNumber = result
            
            const parameter={
                param: {
                    phone:this.member.memberPhone,
                    certifyNumber:  this.certifyBeforeNumber
                }
            }
	  	    this.$store.dispatch("REQUEST_MEMBER_CERTIFY", parameter).then(data => {
                  
				if (data.result) {
                    alert("인증번호를 전송하였습니다.")
                    this.certifyTransFlag = true
					
				}
			})

        },
        validataion(){
            //if(!this.emailCheck(this.member.id)){
            //    return false;
            //}

             if(!(this.member.id.length>=5 && this.member.id.length<=12)){
                alert('정확한 아이디를 입력해 주세요.');
                return false;
            }
            if(this.member.memberPw===null || this.member.memberPw===undefined || this.member.memberPw==='' ){
                alert('비밀번호를 입력해 주세요.');
                return false;
            }

            if(this.member.memberPw!==this.memberPwRe ){
                alert('비밀번호확인이 일치하지 않습니다.');
                return false;
            }

            
            if(this.member.memberName===null || this.member.memberName===undefined || this.member.memberName==='' ){
                alert('이름을 입력해 주세요.');
                return false;
            }

            
            
            if(this.member.memberPhone===null || this.member.memberPhone===undefined || this.member.memberPhone==='' ){
                alert('전화번호를 입력해 주세요.');
                return false;
            }
            if(!this.phoneCheck(this.member.memberPhone)){

                return;
            }
            if(!this.certifyTransFlag && !this.certifyCertifyFlag){
                alert("인증을 진행해 주세요.");
                return
            }
            if(this.member.memberCorpCode.codeId===null || this.member.memberCorpCode.codeId===undefined || this.member.memberCorpCode.codeId===''){
                alert("상조회사를 선택해 주세요.");
                return
            }

                    
            let pwCheckFlag = false
            for(let i=0;i<this.getCodeList('CORP').length;i++){
                if(this.getCodeList('CORP')[i].value === this.member.memberCorpCode.codeId){
                if(this.getCodeList('CORP')[i].codeSubText === this.corpPassword){
                    pwCheckFlag = true
                }
                }
            }
            if(!pwCheckFlag){
                alert("상조회사 비밀번호를 확인해 주세요.");
                return
            }

            return true;
            
        },
        submitTerm(){
            var regType1 = /^[A-Za-z0-9+]*$/;
            if (!regType1.test(this.member.id)) { 
                alert('아이디는 숫자와 영어만 입력 가능합니다.'); 
                return;
            }
            if(!this.validataion()){
                return
            }
			this.$store.commit('START_DEEMED');
            this.agreeFlag =true
        },    
        submit(val){
            this.agreeFlag = false
			this.$store.commit('END_DEEMED');
            if(!this.validataion()){
                return
            }
            
            if(this.masterFlag){
                this.member.memberCorpMaster= 'Y'
                this.member.memberUseYN.codeId='SUCCESS-1'//마스터 상주는 대기
            }else{
                this.member.memberCorpMaster= 'N'
                this.member.memberUseYN.codeId='SUCCESS-0'//마스터 상주는 대기
            }

            this.member.memberMarketingAgree= val
            const parameter={
                member: this.member,
                files: this.files
            }
	  	    this.$store.dispatch("REQUEST_JOIN", parameter) 
        }
    }
}
</script>

